@import url("https://fonts.googleapis.com/css?family=Rubik:300&subset=cyrillic");
.full-width {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  max-width: initial; }

.title {
  text-align: center;
  text-transform: uppercase; }

.font-black {
  color: #1C1D21; }

.font-dark {
  color: #31353D; }

.hidden {
  opacity: 0; }

.relative {
  position: relative; }

.delay-0 {
  transition-delay: 0s !important; }

.delay-1 {
  transition-delay: 0.1s !important; }

.delay-2 {
  transition-delay: 0.2s !important; }

.delay-3 {
  transition-delay: 0.3s !important; }

.delay-4 {
  transition-delay: 0.4s !important; }

.delay-5 {
  transition-delay: 0.5s !important; }

.delay-6 {
  transition-delay: 0.6s !important; }

.delay-7 {
  transition-delay: 0.7s !important; }

.delay-8 {
  transition-delay: 0.8s !important; }

.delay-9 {
  transition-delay: 0.9s !important; }

.delay-10 {
  transition-delay: 1s !important; }

.delay-11 {
  transition-delay: 1.1s !important; }

.delay-12 {
  transition-delay: 1.2s !important; }

.delay-13 {
  transition-delay: 1.3s !important; }

.delay-14 {
  transition-delay: 1.4s !important; }

.delay-15 {
  transition-delay: 1.5s !important; }

.delay-16 {
  transition-delay: 1.6s !important; }

.delay-17 {
  transition-delay: 1.7s !important; }

.delay-18 {
  transition-delay: 1.8s !important; }

.delay-19 {
  transition-delay: 1.9s !important; }

.delay-20 {
  transition-delay: 2s !important; }

.button {
  border: 2px solid;
  padding: 10px;
  text-decoration: none;
  outline: none; }

.button:focus {
  border: 2px solid #92cdcf;
  color: #92cdcf; }

.user-select {
  user-select: all; }

* {
  font-family: 'Rubik', sans-serif;
  font-weight: 300;
  font-size: 20px;
  user-select: none;
  color: #eeeff7; }

h1 {
  font-size: 70px;
  margin: 0; }

h2 {
  font-size: 40px;
  margin: 0; }

a:hover {
  color: #92cdcf; }

.home {
  height: 100vh;
  background: url("../img/header.jpg") bottom center;
  background-size: cover;
  position: relative; }

.main {
  height: 100%;
  overflow: hidden; }

.about {
  background: #eeeff7; }

.about .row {
  min-height: 600px; }

.portfolio {
  height: 100%;
  background: #1C1D21; }

.contacts {
  background: #31353D;
  position: relative; }

.footer {
  padding: 30px;
  background: #3a4b66; }

.h-name {
  position: absolute;
  top: 47%;
  left: 0;
  right: 0;
  margin: auto 0;
  transform: translateY(-50%); }

.h-name-container {
  border: 1px solid;
  display: inline-block;
  background-color: rgba(28, 29, 33, 0.2);
  padding: 0px 25px;
  margin: 5px 0;
  transition: 0.5s; }

.h-name-char {
  letter-spacing: 2px;
  display: inline-block;
  font-size: 70px;
  opacity: 1;
  transition: 0.5s; }

.h-name-char-hidden {
  opacity: 0;
  transform: scale(5); }

.h-profession {
  position: absolute;
  top: 90%;
  left: 0;
  right: 0;
  opacity: 1;
  transition: 1s;
  font-size: 30px; }

.h-profession-hidden {
  top: 100%;
  opacity: 0; }

@media screen and (max-width: 480px) {
  .h-name-char {
    font-size: 50px; } }

@media screen and (max-width: 359px) {
  .h-name-char {
    font-size: 45px;
    letter-spacing: 0; } }

.about-text {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  transform: translateY(-50%);
  margin: 0 50px; }

.about-text p {
  text-align: center;
  max-width: 1000px;
  margin: auto; }

.about-text h2 {
  margin-bottom: 30px; }
  .about-text h2::before, .about-text h2::after {
    content: '';
    display: inline-block;
    vertical-align: middle;
    box-sizing: border-box;
    width: 20%;
    height: 3px;
    background: #31353D;
    border: solid #eeeff7;
    border-width: 0 10px; }
  .about-text h2::before {
    margin-left: -100%; }
  .about-text h2::after {
    margin-right: -100%; }

.gear-container {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  transform: translateY(-50%); }

.gear {
  margin: 5px; }

.gear-1 {
  transform: translateX(-110%); }

.gear-2 {
  transform: translateX(-10%); }

.gear-3 {
  transform: translateX(-110%); }

.gear-4 {
  transform: translateX(110%); }

.gear-5 {
  transform: translateX(10%); }

.gear-6 {
  transform: translateX(110%); }

.portfolio h2 {
  padding: 50px 0; }
  .portfolio h2::before, .portfolio h2::after {
    content: '';
    display: inline-block;
    vertical-align: middle;
    box-sizing: border-box;
    width: 20%;
    height: 3px;
    background: #eeeff7;
    border: solid #1C1D21;
    border-width: 0 10px; }
  .portfolio h2::before {
    margin-left: -100%; }
  .portfolio h2::after {
    margin-right: -100%; }

.portfolio-container {
  height: 100vh; }

.portfolio-container .column {
  padding: 0; }

.project-container {
  overflow: hidden;
  position: relative; }

.project-bg {
  background: black;
  opacity: 0;
  width: 100%;
  height: 100%;
  transition: 0.5s; }

.project {
  position: absolute;
  filter: grayscale(80%);
  transition: 0.5s; }

.project-1 {
  height: 100%;
  width: 100%;
  background: url("../img/portfolio/web-dev.jpg") top center;
  background-size: cover; }

.project-2 {
  height: 100%;
  width: 100%;
  background: url("../img/portfolio/photographer.jpg") top center;
  background-size: cover; }

.project-3 {
  height: 100%;
  width: 100%;
  background: url("../img/portfolio/emploi.jpg") top center;
  background-size: cover; }

.project-4 {
  height: 100%;
  width: 100%;
  background: url("../img/portfolio/endaument.jpg") top center;
  background-size: cover; }

.project-link-container {
  z-index: 2;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  text-align: center; }
  .project-link-container::before {
    height: 100%;
    display: inline-block;
    vertical-align: middle;
    content: ''; }

.project-link {
  display: inline-block;
  vertical-align: middle;
  text-align: left;
  font-size: 20px;
  opacity: 0;
  transform: translateY(40%); }

.project-container:hover .project {
  filter: grayscale(0%) blur(4px);
  transform: scale(1.1, 1.1); }

.project-container:hover .project-link {
  opacity: 1;
  transform: translateY(0%); }

.project-container:hover .project-bg {
  opacity: 0.5; }

.github-link {
  text-align: center;
  font-size: 16px;
  padding: 20px; }

.github-link a {
  font-size: 16px; }

.contacts {
  padding: 100px 0;
  text-align: center; }
  .contacts h2 {
    margin: 0 0 50px; }
    .contacts h2::before, .contacts h2::after {
      content: '';
      display: inline-block;
      vertical-align: middle;
      box-sizing: border-box;
      width: 200px;
      height: 3px;
      background: #eeeff7;
      border: solid #31353D;
      border-width: 0 10px; }
    .contacts h2::before {
      margin-left: -100%; }
    .contacts h2::after {
      margin-right: -100%; }

.contact-mail {
  margin-top: 30px; }

.footer p {
  text-align: center; }
